module.exports = function (hljs) {
  var IDENT_RE_RU = '[a-zA-Z--][a-zA-Z0-9_--]*';
  var OneS_KEYWORDS = '          ' + '        ' + '            ' + ' ';
  var OneS_BUILT_IN = 'ansitooem oemtoansi    ' + '      ' + '      ' + '     ' + '    ' + '     ' + '     ' + '     ' + '      10  ' + '     ' + '      ' + '      ' + '       ' + '     ' + '     ' + '    ' + '      ' + '     ' + '    ' + '        ' + '       ' + '     ' + '      ' + '     ';
  var DQUOTE = {
    className: 'dquote',
    begin: '""'
  };
  var STR_START = {
    className: 'string',
    begin: '"',
    end: '"|$',
    contains: [DQUOTE]
  };
  var STR_CONT = {
    className: 'string',
    begin: '\\|',
    end: '"|$',
    contains: [DQUOTE]
  };
  return {
    case_insensitive: true,
    lexemes: IDENT_RE_RU,
    keywords: {
      keyword: OneS_KEYWORDS,
      built_in: OneS_BUILT_IN
    },
    contains: [hljs.C_LINE_COMMENT_MODE, hljs.NUMBER_MODE, STR_START, STR_CONT, {
      className: 'function',
      begin: '(|)',
      end: '$',
      lexemes: IDENT_RE_RU,
      keywords: ' ',
      contains: [hljs.inherit(hljs.TITLE_MODE, {
        begin: IDENT_RE_RU
      }), {
        className: 'tail',
        endsWithParent: true,
        contains: [{
          className: 'params',
          begin: '\\(',
          end: '\\)',
          lexemes: IDENT_RE_RU,
          keywords: '',
          contains: [STR_START, STR_CONT]
        }, {
          className: 'export',
          begin: '',
          endsWithParent: true,
          lexemes: IDENT_RE_RU,
          keywords: '',
          contains: [hljs.C_LINE_COMMENT_MODE]
        }]
      }, hljs.C_LINE_COMMENT_MODE]
    }, {
      className: 'preprocessor',
      begin: '#',
      end: '$'
    }, {
      className: 'date',
      begin: '\'\\d{2}\\.\\d{2}\\.(\\d{2}|\\d{4})\''
    }]
  };
};